import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';

class Page extends PureComponent {
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Slick </h1>
                <blockquote className='page-tip'>
                    <br/>
                    
                </blockquote>

                <h1 className='page-h1'></h1>

                <Row>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            {<Demo1 />}
                        </Label>
                        <Label className='code-box'>
                            <Demo3></Demo3>
                        </Label>
                        <Label className='code-box'>
                            <Demo5></Demo5>
                        </Label>
                    </Col>
                    <Col grid={0.5} className='code-col'>
                        <Label className='code-box'>
                            <Demo2></Demo2>
                        </Label>
                        <Label className='code-box'>
                            <Demo4></Demo4>
                        </Label>
                    </Col>
                </Row>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Slick</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'current', desc: '', type: 'number', default: '0'},
                    {param: 'layout', desc: 'horizontalvertical', type: 'string', default: 'horizontal'},
                    {param: 'autoPlay', desc: '', type: 'boolean', default: 'false'},
                    {param: 'effect', desc: 'slidefadenormal', type: 'string', default: 'slide'},
                    {param: 'onShow', desc: '', type: 'function', default: ''},
                    {param: 'onShown', desc: '', type: 'function', default: ''},
                    {param: 'delay', desc: '', type: 'number', default: '3000'}
                ]}></Table>

                <h3 className='page-h3'>Methods</h3>

                <ul className='code-methods'>
                    <li>
                        <i>play()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>getActive()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {'Number'} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>jumpTo(index, callback)</i>
                        <span></span>
                        <ul>
                            <li>
                                index {'Number'} 
                            </li>
                            <li>
                                callback {'function'}  
                            </li>
                        </ul>
                    </li>
                </ul>

                <h3 className='page-h3'>Slick.Item</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'active', desc: '', type: 'boolean', default: 'false'}
                ]}></Table>
            </div>
        );
    }
}

export default Page;



